<template>
  
  <div class="place-wrap">
    <!--  -->
    <header class="header-top">
      <header-top></header-top>
    </header>
    <!--  -->
    <div class="con-wrap">
      <!--  -->
      <div class="swiper-box">
        <div class="swiper-container">
          <div class="swiper-wrapper">
              <!--  -->
              <div class="swiper-slide" v-for="(list, index) in place" :key="index">
                  <div class="img-box" :style="{background:list.bg}">
                  </div>
                  <p>{{list.label}}</p>
              </div>
          </div>
          <div class="swiper-pagination swiper-pagination-white"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Swiper from 'swiper'
  import 'swiper/dist/css/swiper.min.css'
  import headerTop from '../../components/header/header'
  export default {
    name: '',
    data() {
      return {
        bg: '',
        place: [
          {
            value: 'yunnan',
            label: '',
            bg: 'red'
          },
          {
            value: 'yunnan',
            label: '',
            bg: 'green'
          },
          {
            value: 'yunnan',
            label: '',
            bg: 'pink'
          }
        ]
      }
    },
    mounted() {
      new Swiper('.swiper-container', {
        pagination: {
          el: '.swiper-pagination'
        },
        paginationClickable: true,
        loop: true,
        autoplay: {
          delay: 5000,
          stopOnLastSlide: false,
          disableOnInteraction: true
        },
        on: {
          slideChangeTransitionEnd: function(){
            // console.log(this.activeIndex)
          },
          transitionEnd: function(){
            // console.log(this.activeIndex,"--",this.place)
          }
        },
        effect: 'coverflow',
        grabCursor: true,
        centeredSlides: true,
        slidesPerView: 'auto',
        coverflowEffect: {
          rotate: 50,
          stretch: 0,
          depth: 100,
          modifier: 1,
          slideShadows: false
        }
      })
    },
    components: {
      Swiper,
      headerTop
    },
    methods: {
    }
  }
</script>

<style scoped lang="less">
@import '../../assets/style/color.less';
@import '../../assets/style/index.less';
.place-wrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: @blue-dark;
  .header-top {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: @header-top-login;
    z-index: 10;
  }
  .con-wrap {
    z-index: 9;    
    position: absolute;
    width: 100%;
    height: 100%;
  }
}
.swiper-box{
  position: absolute;
  bottom: 60px;
  left: 0;
  height: 60%;
  width: 100%;
}
.swiper-slide{
  width: 300px;
  height: 460px;
  .img-box{
    width: 100%;
    height: 330px;
  }
  p{
    font-size: 22px;
    text-align: center;
    background: @white;
    padding: 24px 0;
  }
}

</style>
<style lang="less">
.place-wrap{
  .swiper-container {
  // .swiper-wrapper {
  // }
  // .swiper-slide {
  // }
  .swiper-pagination-bullet {
      width: 30px;
      height: 2px;
      margin: 0px;
      border-radius: 0%;
  }
}
}

</style>




